import Vue from 'vue'
import Router from 'vue-router'
import { Home, AboutUs, Testimonials, Courses, ContactUs } from '@/components'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/about-us',
      name: 'about_us',
      component: AboutUs
    },
    {
      path: '/testimonials',
      name: 'testimonials',
      component: Testimonials
    },
    {
      path: '/courses',
      name: 'courses',
      component: Courses
    },
    {
      path: '/contact-us',
      name: 'contact_us',
      component: ContactUs
    }
  ]
})



